
/* Risorse comuni. */
@import url('./Resources/Fonts/Fonts.scss');

/* Impostazioni globali. */
#root {
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;

	background-color: white;

	font-family: 'Mooli Regular';
}

/* Applicazione. */
.App {
	position: relative;
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;

	& > header {
		width: 100%;
		height: 96px;

		background-color: silver;

		& > .app-logo {
			content: url('./Resources/AppLogo.png');
			height: 100%;

			margin-left: 8px;
		}

		& > .app-version {
			position: absolute;
			top: 3px;
			right: 3px;

			color: gray;

			font-family: monospace;
			font-size: 12px;
		}
	}

	& > .Workplace {
		width: 100%;
		height: 100%;

		overflow: scroll;
		overflow-x: hidden;

		display: flex;
		align-items: flex-start;
		justify-content: center;

		& > .DocumentEditor {
			width: 80%;
			height: fit-content;
			margin-top: 6px;
			margin-bottom: 168px;

			display: flex;
			flex-direction: column;
			gap: 3px;

			& .RegionGroup {
				width: 100%;
				height: fit-content;

				display: flex;
				flex-direction: row;
				gap: 3px;
			}

			& .Region {
				width: 100%;
				height: fit-content;
				min-height: 24px;

				padding: 3px;

				display: flex;
				flex-direction: column;

				background-color: slategray;
				border: none;
				border-radius: 3px;

				color: black;

				& > header
				{
					width: 100%;
					height: 24px;
			
					padding-left: 3px;
			
					display: flex;
					flex-direction: row;
					align-items: center;
					gap: 3px;
			
					& > svg, & > img
					{
						width: 24px;
						height: 24px;
					}
			
					& > label
					{
						width: fit-content;
						height: fit-content;
						font-size: 16px;
					}
				}

				& > .sections-list
				{
					width: 100%;
					height: fit-content;

					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: center;
					gap: 3px;

					& > .item
					{
						position: relative;
						width: 100%;
						height: fit-content;

						padding: 3px;
						padding-top: 6px;
						padding-bottom: 6px;
						padding-right: 28px;

						display: flex;
						flex-direction: column;
						gap: 3px;

						border: 1px solid black;
						border-radius: 3px;

						& > footer
						{
							position: absolute;
							width: 24px;
							height: 100%;
							top: 0px;
							right: 2px;

							display: flex;
							flex-direction: column;
							justify-content: center;
							align-items: center;

							& > button
							{
								width: 100%;
								height: 24px;

								& > svg
								{
									width: 100%;
									height: 100%;
								}
							}
						}
					}

					& > footer
					{
						& > button
						{
							width: 96px;
							height: 24px;

							& > svg
							{
								width: 24px;
								height: 100%;
							}
						}
					}
				}

				& .user {
					width: 100%;
				}
			}

			& > footer
			{
				width: 100%;
				margin-top: 32px;

				display: flex;
				flex-direction: row;
				justify-content: flex-end;
				gap: 12px;

				& > button
				{
					width: 192px;
					height: 32px;

					display: flex;
					flex-direction: row;
					justify-content: center;
					align-items: center;
					gap: 3px;

					// border: 1px solid black;
					// border-radius: 3px;

					& > svg
					{
						width: 24px;
						height: 24px;
					}

					& > label
					{
						width: fit-content;
						height: fit-content;
						color: black;
					}

					&.export-pdf
					{
						visibility: hidden;
						color: red;
					}

					&.export-word
					{
						color: blue;
					}
				}
			}
		}
	}
}

.user
{
	position: relative;
	width: fit-content;
	height: fit-content;
	min-width: 2px;
	min-height: 24px;

	display: flex;
	flex-direction: column;
	gap: 3px;

	&.TextArea
	{
		padding: 3px;

		& > textarea
		{
			resize: none;
			outline: none;
		}
	}
}
